import { useState } from "react";
import Button from 'react-bootstrap/Button';

const Form = ({onUpdate})=>{
    const includesHangul = (text) => /[-|-|-]/i.test(text);
    const [value, setValue] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    
    const translate = (e)=>{
        const userValue = e.target.value; //input     .
        setErrorMessage("") //      .
        
        if(includesHangul(userValue)){
            setErrorMessage("You cannot input Korean!");
        }
        setValue(userValue.toUpperCase()); //  .
    }

    const create = (e)=>{
        e.preventDefault();
        setErrorMessage("")
        if(value === ""){
            setErrorMessage("It cannot be submitted with an empty value!")
            return; //return     onUpdate(value)     return .
        }
        onUpdate(value);
    }

    return(
        <form onSubmit={create}>
            <input
            className = "input_style"
            type ="text"
            placeholder="Please enter English language."
            value={value}
            onChange ={translate}
            style={{marginRight:"20px"}}
            />
            <Button variant="outline-info">Create</Button>
            <p>{errorMessage}</p>
        </form>
    )
}

export default Form;